import React, { useEffect, Fragment, useRef } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";

import GameFrame from "./component/GameFrame";
import Background from "./component/Background";
import Machine from "./component/Machine";

const align_style = {
  position: "relative",
  zIndex: 1,
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
};

const MainPage = () => {
  const machineRef = useRef(null);

  useEffect(() => {
    // Scroll into the GameFrame element when the component mounts
    const gameFrameElement = document.getElementById("target-iframe");
    if (gameFrameElement) {
      gameFrameElement.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    // 更新 Background 高度
    const updateHeight = () => {
      const machineElement = machineRef.current;
      const canvasElement = document.getElementById("background");
      const bannerSpace = document.getElementById("bannerSpace");
      const gameSpace = document.getElementById("gameSpace");

      if (machineElement && canvasElement) {
        const height = machineElement.offsetHeight;
        canvasElement.style.height = `${height + 50}px`;
        bannerSpace.style.height = `${0.12 * height}px`;
        gameSpace.style.height = `${0.23 * height}px`;
      }
    };

    // 等待一小段時間後再執行第一次更新
    setTimeout(updateHeight, 100);

    // 監聽視窗大小變化
    window.addEventListener("resize", updateHeight);

    // 清理函數
    return () => {
      window.removeEventListener("resize", updateHeight);
    };
  }, []);

  return (
    <Fragment>
      <div className="machine">
        <div>
          <Machine width="100%" scale="1" ref={machineRef} />
        </div>
        <div
          id="bannerSpace"
          style={{ marginTop: "20px", height: "20vh", ...align_style }}
        ></div>
        <div id="gameSpace" style={{ paddingTop: "5%", ...align_style }}>
          <GameFrame src="game.html" width="960" height="750" />
        </div>
      </div>
      <Background />
    </Fragment>
  );
};

ReactDOM.render(<MainPage />, document.getElementById("root"));

function gameFrame_size() {
  const game_frame = document.getElementById("target-iframe");
  if (game_frame) {
    if (window.innerWidth > window.innerHeight) {
      // 橫向
      if (window.innerHeight > "768") {
        game_frame.style.height = "75vh";
      } else {
        game_frame.style.height = "85vh";
      }
    } else {
      game_frame.style.height = "50vw"; // 直向
    }
  }
}

// 初始化高度
gameFrame_size();

// 監聽視窗大小調整事件
window.addEventListener("resize", gameFrame_size);

